import BrowserWindow from "sketch-module-web-view";

const options = {
  identifier: "overrideitWebView",
  width: 250,
  height: 600,
  frame: true,
  minHeight: 80,
  minWidth: 250,
  minimizable: false,
  maximizable: false,
  useContentSize: true,
  movable: true,
  alwaysOnTop: true,
  center: false,
  show: false
};

export default (onRun = function(Context) {
  //transparent: true
  var threadDictionary = NSThread.mainThread().threadDictionary();
  var browserWindow = threadDictionary["overrideitWebView"];

  if (Context.selection) {
    if (!browserWindow) {
      browserWindow = new BrowserWindow(options);
      browserWindow.loadURL(require("../assets/index.html"));
      browserWindow["document"] = Context.document;
      //browserWindow.loadURL("http://localhost:8080/");
      browserWindow.show();
    }
  }
  if (browserWindow) {
    if (Context.actionContext) {
      var action = Context.actionContext;
      var context = {};
      context["document"] = action.document;
      context["selection"] = action.newSelection;
    } else {
      var context = Context;
    }
    if (browserWindow.document != context.document) {
      browserWindow.close();
      threadDictionary.removeObjectForKey("overrideitWebView");
    } else {
      global.overrides = {};
      global.symbolsOverrides = {};
      global.importableSymbolsOverridesDicIdMap = {};
      var symbolInstance = context.selection[0];
      //global.overrides = loadOverridesForSelection(symbolInstance);
      global.overrides = loadOverridesForSelection(symbolInstance);

      browserWindow.webContents.executeJavaScript(
        `changeSelection(${JSON.stringify(global.overrides)},${JSON.stringify(
          String(symbolInstance.objectID())
        )})`
      );

      browserWindow.webContents.on("posScreen", function(width, height) {
        browserWindow.setPosition(width - 250, height - 630, false);
      });

      browserWindow.webContents.on("moveWindow", function(posX, posY) {
        browserWindow.setPosition(posX, posY, true);
      });

      browserWindow.webContents.on("openExternal", function(url) {
        NSWorkspace.sharedWorkspace().openURL(NSURL.URLWithString(url));
      });

      browserWindow.webContents.on("closeWindow", function() {
        browserWindow.close();
        threadDictionary.removeObjectForKey("overrideitWebView");
      });

      browserWindow.webContents.on("setOverride", function(
        overridePoint,
        value,
        symbolInstanceID
      ) {
        var symbolInstance = context.document
          .documentData()
          .layerWithID(symbolInstanceID);

        setOverrides(symbolInstance, overridePoint, value);
        var valueLength = global.overrides[
          overridePoint
        ].overrideCurrentValue.value.length();
        var searchQuery =
          global.overrides[overridePoint].overrideCurrentValue.searchQuery;
        global.overrides[overridePoint].overrideCurrentValue.value = value;
        global.overrides[overridePoint].overrideCurrentValue.searchQuery =
          searchQuery.substring(0, searchQuery.length() - valueLength - 1) +
          value;
        browserWindow.webContents.executeJavaScript(
          `getOverrides(${JSON.stringify(global.overrides)},${JSON.stringify(
            String(symbolInstance.objectID())
          )})`
        );
      });

      browserWindow.webContents.on("setOverrideImage", function(
        overridePoint,
        symbolInstanceID
      ) {
        var symbolInstance = context.document
          .documentData()
          .layerWithID(symbolInstanceID);
        var value = getImageValue();
        if (value != 0) {
          setOverrides(symbolInstance, overridePoint, value);
          global.overrides[overridePoint].overrideCurrentValue.value = String(
            getImageData64(value.data())
          );
          browserWindow.webContents.executeJavaScript(
            `getOverrides(${JSON.stringify(global.overrides)},${JSON.stringify(
              String(symbolInstance.objectID())
            )})`
          );
        }
      });

      browserWindow.webContents.on("getSymbolOverridesData", function(layerID) {
        var layer = getLayerByID(layerID);
        var layerFrame = layer.frame().size();
        var layerWidthAndHeight =
          String(layerFrame.width) + "-" + String(layerFrame.height);
        if (!global.symbolsOverrides[layerWidthAndHeight]) {
          var getMatchedSymbolArrayAndDic = getMatchedSymbols(
            context,
            layer.symbolMaster()
          );
          global.symbolsOverrides[layerWidthAndHeight] =
            getMatchedSymbolArrayAndDic[0];
          global.importableSymbolsOverridesDicIdMap[layerWidthAndHeight] =
            getMatchedSymbolArrayAndDic[1];
        }
        browserWindow.webContents.executeJavaScript(
          `getSymbolOverrides(${JSON.stringify(
            global.symbolsOverrides[layerWidthAndHeight]
          )})`
        );
      });

      browserWindow.webContents.on("setSymbolOverride", function(
        overridePoint,
        libraryID,
        value,
        symbolInstanceID
      ) {
        var symbolInstance = context.document
          .documentData()
          .layerWithID(symbolInstanceID);
        if (value == "") {
          setOverrides(symbolInstance, overridePoint, "");
        } else {
          var symbol = symbolInLibraryWithIDs(value, libraryID);
          setOverrides(symbolInstance, overridePoint, symbol.symbolID());
        }
        var overrides = getOverrides(symbolInstance);
        browserWindow.webContents.executeJavaScript(
          `getOverrides(${JSON.stringify(overrides)},${JSON.stringify(
            String(symbolInstance.objectID())
          )})`
        );
      });

      browserWindow.webContents.on("updateOverride", function() {
        var overrides = loadOverridesForSelection(symbolInstance);
        browserWindow.webContents.executeJavaScript(
          `getOverrides(${JSON.stringify(overrides)},${JSON.stringify(
            String(symbolInstance.objectID())
          )})`
        );
      });

      browserWindow.on("closed", () => {
        threadDictionary.removeObjectForKey("overrideitWebView");
      });

      function loadOverridesForSelection(symbolInstance) {
        if (symbolInstance.class() == MSSymbolInstance) {
          return getOverrides(
            symbolInstance,
            String(symbolInstance.objectID())
          );
        }
      }

      function setImgfromUrl(url) {
        var image = NSImage.alloc().initWithContentsOfURL(url);
        var imageData = MSImageData.alloc().initWithImage(image, true);
        object.setImage(imageData);
      }

      // setTimeout(function(){
      //   browserWindow.webContents.executeJavaScript(`getOverrides(hi)`)
      //  }, 2000);

      // browserWindow.loadURL('http://localhost:8080/', function(){
      //     browserWindow.webContents.executeJavaScript(`sayHi("hello")`)

      //   })

      function getOverrides(symbolInstance) {
        var overrides = {};
        var availableOverrides = symbolInstance.availableOverrides();
        for (var i = 0; i < availableOverrides.count(); i++) {
          if (!availableOverrides[i].affectedLayer().isLocked()) {
            var overridePoint = availableOverrides[i].overridePoint();
            if (
              overridePoint.isSymbolOverride() &&
              availableOverrides[i].currentValue()
            ) {
              var symbolOverrides = getOverridesFromSymbolOverride(
                availableOverrides[i],
                {},
                true,
                availableOverrides[i].overridePoint().layerName()
              );
              overrides = Object.assign({}, overrides, symbolOverrides);
            } else {
              var overrideOb = overrideObject(availableOverrides[i], true, "");
              var override = overrideOb[0];
              var searchQuery = overrideOb[1];
              overrides[override.overrideID] = override;
            }
          }
        }
        return sortOverrides(symbolInstance, overrides);
      }

      function sortOverrides(symbolInstance, overrides) {
        var overridePoints = symbolInstance.overridePoints();
        var sortedOverrides = {};
        for (var i = 0; i < overridePoints.count(); i++) {
          sortedOverrides[overridePoints[i]] = overrides[overridePoints[i]];
        }
        return sortedOverrides;
      }

      function getOverridesFromSymbolOverride(
        symbolOverride,
        overrides,
        isParent,
        searchQueryParent
      ) {
        var availableOverrides = symbolOverride.children();
        var overrideOb = overrideObject(
          symbolOverride,
          isParent,
          searchQueryParent
        );
        var override = overrideOb[0];
        var searchQuery = overrideOb[1];
        overrides[override.overrideID] = override;
        for (var i = 0; i < availableOverrides.count(); i++) {
          if (!availableOverrides[i].affectedLayer().isLocked()) {
            var overridePoint = availableOverrides[i].overridePoint();
            if (
              overridePoint.isSymbolOverride() &&
              availableOverrides[i].currentValue() != ""
            ) {
              var symbolOverrides = getOverridesFromSymbolOverride(
                availableOverrides[i],
                overrides,
                false,
                searchQuery
              );
              overrides = Object.assign({}, overrides, symbolOverrides);
            } else {
              var overrideOb = overrideObject(
                availableOverrides[i],
                false,
                searchQueryParent
              );
              var override = overrideOb[0];
              overrides[override.overrideID] = override;
            }
          }
        }

        return overrides;
      }

      function getImageData64(data) {
        var imageData = data;
        var mimeType = "image/png";
        return NSString.stringWithFormat(
          "data:%@;base64,%@",
          mimeType,
          imageData.base64EncodedStringWithOptions(0)
        );
      }

      function getImageFromSymbol(symbol) {
        var ancestry = MSImmutableLayerAncestry.ancestryWithMSLayer(symbol);
        var exportRequest = MSExportRequest.exportRequestsFromLayerAncestry_(
          ancestry
        ).firstObject();
        exportRequest.format = "png";
        var scaleRate = 0;
        if (
          exportRequest.rect().size.width > exportRequest.rect().size.height
        ) {
          scaleRate = 20 / exportRequest.rect().size.width;
        } else {
          scaleRate = 20 / exportRequest.rect().size.height;
        }
        exportRequest.scale = scaleRate;
        var colorSpace = NSColorSpace.sRGBColorSpace();
        var exporter = MSExporter.exporterForRequest_colorSpace_(
          exportRequest,
          colorSpace
        );
        var data = exporter.data();
        return getImageData64(data);
      }

      function overrideObject(availableOverride, isParent, searchQuery) {
        var currentValue = {};
        var searchQueryResult = String(
          String(searchQuery) +
            " " +
            String(availableOverride.overridePoint().layerName())
        );
        if (availableOverride.overridePoint().property() == "image") {
          currentValue["value"] = String(
            getImageData64(availableOverride.currentValue().data())
          );
          currentValue["searchQuery"] = String(searchQueryResult + " image");
        } else if (availableOverride.overridePoint().property() == "symbolID") {
          if (availableOverride.currentValue() == "") {
            var symbolID = availableOverride.defaultValue();
            currentValue = {};
            currentValue["name"] = "none";
            currentValue["symbolID"] = String(symbolID);
            currentValue["layerID"] = String(
              availableOverride.overridePoint().layerID()
            );
            currentValue["Thumbnail"] = "";
            currentValue["searchQuery"] = String(searchQueryResult + " ");
          } else {
            var symbolID = availableOverride.currentValue();
            var symbol = context.document.documentData().symbolWithID(symbolID);
            currentValue["name"] = String(symbol.name());
            currentValue["symbolID"] = String(symbolID);
            currentValue["layerID"] = String(
              availableOverride.overridePoint().layerID()
            );
            currentValue["Thumbnail"] = String(getImageFromSymbol(symbol));
            currentValue["searchQuery"] = String(
              searchQueryResult + " " + currentValue.name
            );
          }
        } else {
          currentValue["value"] = String(availableOverride.currentValue());
          currentValue["searchQuery"] = String(
            searchQueryResult + " " + currentValue.value
          );
        }
        var override = {
          overrideTitle: String(availableOverride.overridePoint().layerName()),
          overrideType: String(availableOverride.overridePoint().property()),
          overrideDefualtValue: String(availableOverride.defaultValue()),
          overrideCurrentValue: currentValue,
          overrideID: String(availableOverride.overridePoint().name()),
          overridePoint: availableOverride.overridePoint(),
          isParent: isParent
        };
        return [override, searchQueryResult];
      }

      function setOverrides(symbolInstance, overridePoint, value) {
        var overridePointsPredicate = NSPredicate.predicateWithFormat(
          "(name == %@)",
          overridePoint
        );
        var overrides = symbolInstance.overridePoints();
        var matchOverride = overrides.filteredArrayUsingPredicate_(
          overridePointsPredicate
        );
        symbolInstance.setValue_forOverridePoint(value, matchOverride[0]);
        //symbolInstance.setValue_forOverridePoint("WTF",overridePoint)
      }

      function getImageValue() {
        var fileTypes = ["png", "jpg", "tiff"];
        const panel = NSOpenPanel.openPanel();
        panel.setAllowsMultipleSelection(0);
        panel.setCanChooseDirectories(0);
        panel.setCanChooseFiles(1);
        panel.setFloatingPanel(1);
        panel.setMessage("Choose Image");
        var result = panel.runModalForDirectory_file_types(nil, nil, fileTypes);
        if (result != NSOKButton) {
          return 0;
        }
        if (panel.URLs().count() < 1) {
          return 0;
        }
        var image = NSImage.alloc().initWithContentsOfURL(panel.URLs()[0]);
        var imageData = MSImageData.alloc().initWithImage(image);

        return imageData;
      }

      function filterlocalSymbols(symbolArray, symbol) {
        var matchSymbolArray = [];
        var symbolPredicate = NSPredicate.predicateWithFormat(
          "(frame.width == %@) AND (frame.height == %@)",
          symbol.frame().size().width,
          symbol.frame().size().height
        );
        var matchedSymbols = symbolArray.filteredArrayUsingPredicate_(
          symbolPredicate
        );
        for (var i = 0; i < matchedSymbols.count(); i++) {
          var symbolObj = {};
          symbolObj["name"] = String(matchedSymbols[i].name());
          symbolObj["symbolID"] = String(matchedSymbols[i].symbolID());
          symbolObj["Thumbnail"] = String(
            getImageFromSymbol(matchedSymbols[i])
          );
          matchSymbolArray = matchSymbolArray.concat(symbolObj);
        }
        return matchSymbolArray;
      }

      function getLayerByID(layerID) {
        var layer = context.document.documentData().layerWithID(layerID);
        if (layer != null) {
          return layer;
        }
        var libraries = AppController.sharedInstance()
          .librariesController()
          .availableLibraries();
        for (var i = 0; i < libraries.count(); i++) {
          layer = libraries[i].document().layerWithID(layerID);
          if (layer != null) {
            return layer;
          }
        }
        return null;
      }

      function symbolInLibraryWithIDs(symbolID, libraryID) {
        var symbol = context.document.documentData().symbolWithID(symbolID);
        if (symbol) {
          return symbol;
        } else {
          if (libraryID == "local") {
            return context.document.documentData().symbolWithID(symbolID);
          } else {
            var libraries = AppController.sharedInstance()
              .librariesController()
              .availableLibraries();
            for (var i = 0; i < libraries.count(); i++) {
              if (libraries[i].libraryID() == libraryID) {
                var symbol = libraries[i].document().symbolWithID(symbolID);
                return localSymbolForSymbol_inLibrary(symbol, libraries[i]);
              }
            }
          }
        }
        return null;
      }

      function getMatchedSymbols(context, symbol) {
        var libraries = AppController.sharedInstance()
          .librariesController()
          .availableLibraries();
        var matchedSymbolsArray = [];
        var matchedarray = filterlocalSymbols(
          context.document.documentData().localSymbols(),
          symbol
        );
        var importableSymbolsDictionary = {};
        if (matchedarray[0] != undefined) {
          var matchedSymbolObj = {};
          matchedSymbolObj["libraryName"] = "localSymbols";
          matchedSymbolObj["libraryID"] = "local";
          matchedSymbolObj["matchedSymbolArray"] = matchedarray;
          matchedSymbolsArray = matchedSymbolsArray.concat(matchedSymbolObj);
        }
        for (var i = 0; i < libraries.count(); i++) {
          var matchedarrayAndDictionary = getMatchedForgienSymbolForLibrary(
            context,
            symbol,
            libraries[i]
          );
          var matchedarray = matchedarrayAndDictionary[0];
          importableSymbolsDictionary = Object.assign(
            {},
            importableSymbolsDictionary,
            matchedarrayAndDictionary[1]
          );
          if (matchedarray) {
            if (matchedarray[0] != undefined) {
              var matchedSymbolObj = {};
              matchedSymbolObj["libraryName"] = String(libraries[i].name());
              matchedSymbolObj["libraryID"] = String(libraries[i].libraryID());
              matchedSymbolObj["matchedSymbolArray"] = matchedarray;
              matchedSymbolsArray = matchedSymbolsArray.concat(
                matchedSymbolObj
              );
            }
          }
        }
        return [matchedSymbolsArray, importableSymbolsDictionary];
      }

      function getMatchedForgienSymbolForLibrary(context, symbol, library) {
        var importableSymbolsDictionary = {};
        var document = context.document;
        const provider = MSForeignSymbolProvider.alloc().initWithDocument(
          document
        );
        const collector = MSForeignObjectCollector.alloc().initWithProvider(
          provider
        );

        const shareableObjectRefsMap = collector.buildCollectionWithFilter(
          null
        );
        var libraryPredicate = NSPredicate.predicateWithFormat(
          "(library.libraryID == %@)",
          library.libraryID()
        );

        var matchedLibrary = shareableObjectRefsMap.filteredArrayUsingPredicate_(
          libraryPredicate
        );
        if (matchedLibrary.count() > 0) {
          var allSymbols = matchedLibrary[0].objectRefs;
          var symbolPredicate = NSPredicate.predicateWithFormat(
            "(symbolMaster.frame.width == %@) AND (symbolMaster.frame.height == %@)",
            symbol.frame().size().width,
            symbol.frame().size().height
          );
          var matchedSymbols = allSymbols.filteredArrayUsingPredicate_(
            symbolPredicate
          );
          var matchSymbolArray = [];
          for (var i = 0; i < matchedSymbols.count(); i++) {
            var foreignSymbol = matchedSymbols[i].symbolMaster();
            var symbolObj = {};
            importableSymbolsDictionary[foreignSymbol.symbolID()] =
              matchedSymbols[i];
            symbolObj["name"] = String(foreignSymbol.name());
            symbolObj["symbolID"] = String(foreignSymbol.symbolID());
            symbolObj["Thumbnail"] = String(getImageFromSymbol(foreignSymbol));
            matchSymbolArray = matchSymbolArray.concat(symbolObj);
          }

          return [matchSymbolArray, importableSymbolsDictionary];
        }
        return [];
      }
      threadDictionary["overrideitWebView"] = browserWindow;
    }
  }
  function localSymbolForSymbol_inLibrary(symbol, library) {
    if (MSApplicationMetadata.metadata().appVersion >= 50) {
      var shareableObjectReference = MSShareableObjectReference.referenceForShareableObject_inLibrary(
        symbol,
        library
      );

      var importedSymbol = AppController.sharedInstance()
        .librariesController()
        .importShareableObjectReference_intoDocument(
          shareableObjectReference,
          context.document.documentData()
        );
    } else {
      var importedSymbol = AppController.sharedInstance()
        .librariesController()
        .importForeignSymbol_fromLibrary_intoDocument_(
          symbol,
          library,
          context.document.documentData()
        );
    }
    return importedSymbol.symbolMaster();
  }
});
